import React from 'react';
import ReactDOM from 'react-dom';
import { ThemeProvider, createTheme } from 'arwes';

function App() {
    return (
    <ThemeProvider theme={createTheme()}>
        <div>My Blog</div>
    </ThemeProvider>

    );
}

ReactDOM.render(
  <React.StrictMode>
    <App />
  </React.StrictMode>,
  document.getElementById('root')
);
